.encrypzion__features-container__features {

    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    margin: 1rem;
}

.encrypzion__features-container__features-title {

    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}

.encrypzion__features-container__features h1 {

    font-family: var(--font-family);
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    line-height: 30px;


    letter-spacing: -0.04em;

    color: #e6e7eb;

}

.encrypzion__features-container__features-title div {

    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: .25rem;
}

.encrypzion__features-container__features-text {

    flex: 2;
    max-width: 390px;
    display: flex;
}

.encrypzion__features-container__features-text p {

    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {

    .encrypzion__features-container__features-title h1 {

        font-size: 14px;
        line-height: 22px;
    }

    .encrypzion__features-container__features-text p {

        font-size: 12px;
        line-height: 20px;
    }

    .encrypzion__features-container__features {

        margin: 1rem 0;
    }
}