.whatencrypzion {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    /* ff 3.6+ */
    background: -moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background: -webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* opera 11.10+ */
    background: -o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* ie 10+ */
    background: -ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.whatencrypzion-feature {

    display: flex;
    margin: 0;
}

/* .whatencrypzion-feature . */

.whatencrypzion-heading {

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    margin: 4rem 0 2rem;
}

.whatencrypzion-heading h1 {

    font-family: var(--font-family);
    font-style: normal;
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    max-width: 510px;
}

.whatencrypzion-heading p {

    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;

    color: var(--color-subtext);
}


.whatencrypzion-container {

    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    margin-top: 2rem;
}

.whatencrypzion-container .whatencrypzion-container__feature {

    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.whatencrypzion-container .whatencrypzion-container__feature-text {

    margin-top: .5rem;
}


@media screen and (max-width: 850px) {
    .whatencrypzion-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .whatencrypzion-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {

    .whatencrypzion {

        margin: 1rem 2rem;
        padding: 1rem;
    }

    .whatencrypzion-feature .whatencrypzion-feature-container__feature {
        flex-direction: column;
        margin: 0;
    }

    .whatencrypzion-feature .whatencrypzion-feature-container_feature-text {
        margin-top: 0.5rem;
    }

    .whatencrypzion-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {

    .whatencrypzion-container .whatencrypzion-feature-container__feature {
        /* margin: 1rem 0; */
        min-width: 100%;
    }
}